* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-display: swap;
  background-color: #fff;
  font-family: Patua One;
  font-style: normal;
  font-weight: 400;
}

body {
  min-height: 100vh;
  justify-content: center;
  display: flex;
}

main {
  width: 1200px;
  background-color: #fff;
  flex-direction: column;
  display: flex;
}

header {
  width: 1200px;
  justify-content: space-around;
  align-items: center;
  margin-top: 50px;
  display: flex;
}

a {
  color: #000;
  text-decoration: none;
}

header .logo {
  margin-top: -10px;
}

header .logo a {
  font-size: 45px;
}

header input {
  font-display: swap;
  text-align: center;
  border: 2px solid #969696;
  border-radius: 5px;
  font-family: Patua One;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  transition: all .7s;
}

header input::placeholder {
  text-align: center;
  color: #777;
  font-size: 16px;
}

label {
  color: #777;
}

.search-submit {
  width: 50px;
  height: 25px;
  color: #777;
  border-radius: 10px;
}

.search-submit:hover {
  color: #000;
  border: 2px solid #555;
}

.search-geo {
  width: 25px;
  height: 25px;
  fill: #777;
  border: 2px solid #969696;
  border-radius: 10px;
  transition: all .7s;
}

.search-geo:hover {
  fill: #000;
  border: 2px solid #555;
}

section {
  flex: 1;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

section .about {
  width: 1200px;
  flex-direction: column;
  display: flex;
  overflow: hidden;
}

section h2 {
  width: 800px;
  height: 49px;
  font-size: 40px;
  display: flex;
}

.search {
  font-family: inherit;
  font-size: inherit;
  text-align: center;
  line-height: inherit;
}

.weather-stats {
  width: 1000px;
  height: 300px;
  opacity: 1;
  background-color: #bebebe;
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.ico-img {
  justify-content: left;
  align-items: flex-start;
  display: flex;
}

.ico-img img {
  width: 45%;
  transition: all .7s;
}

.weather-stat1, .weather-stat2, .weather-stat3 {
  color: #bebebe;
  width: 100%;
  flex-direction: row;
  justify-content: center;
  font-size: 24px;
  display: flex;
}

.weather-ico, .temperature-ico, .wind-ico {
  width: 28%;
  height: 80%;
  opacity: .3;
  background-color: #000;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  transition: all .7s;
  display: flex;
}

.temperature-ico img {
  margin-right: 10px;
}

.weather-ico:hover, .temperature-ico:hover, .wind-ico:hover {
  opacity: 1;
}

.time {
  height: 20px;
}

footer {
  min-height: 100px;
  justify-content: center;
  align-items: flex-end;
  font-size: 14px;
  display: flex;
}

.animated {
  animation-duration: 1.5s;
  animation-fill-mode: both;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fadeIn {
  animation-name: fadeIn;
}

@keyframes flash {
  from, 50%, to {
    opacity: 1;
  }

  25%, 75% {
    opacity: 0;
  }
}

.flash {
  animation-name: flash;
}

@keyframes slideInUp {
  from {
    visibility: visible;
    transform: translate3d(0, 100%, 0);
  }

  to {
    transform: translate3d(0, 0, 0);
  }
}

.slideInUp {
  animation-name: slideInUp;
}

