* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: #fff;
  font-family: 'Patua One';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
}

body {
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

main {
  width: 1200px;
  display: flex;
  flex-direction: column;
  background-color: #fff;
}

header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  width: 1200px;
  margin-top: 50px;
}

a {
  color: #000;
  text-decoration: none;
}

header .logo {
  margin-top: -10px;
}

header .logo a {
  font-size: 45px;
}

header input {
  font-family: 'Patua One';
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  border: 2px solid #969696;
  border-radius: 5px;
  transition: 0.7s;
  font-size: 16px;
  text-align: center;
}

header input::placeholder {
  text-align: center;
  font-size: 16px;
  color: #777;
}

label {
  color: #777;
}

.search-submit {
  width: 50px;
  height: 25px;
  color: #777;
  border-radius: 10px;
}

.search-submit:hover {
  border: 2px solid #555;
  color: #000;
}

.search-geo {
  width: 25px;
  height: 25px;
  border-radius: 10px;
  fill: #777;
  border: 2px solid #969696;
  transition: 0.7s;
}

.search-geo:hover {
  fill: #000;
  border: 2px solid #555;
}

section {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: space-around;
}

section .about {
  width: 1200px;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

section h2 {
  width: 800px;
  height: 49px;
  display: flex;
  font-size: 40px;
}

.search {
  font-family: inherit;
  font-size: inherit;
  text-align: center;
  line-height: inherit;
}

.weather-stats {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #bebebe;
  width: 1000px;
  height: 300px;
  opacity: 1;
}

.ico-img {
  display: flex;
  justify-content: left;
  align-items: flex-start;
}

.ico-img img {
  width: 45%;
  transition: 0.7s;
}

.weather-stat1,
.weather-stat2,
.weather-stat3 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  color: #bebebe;
  font-size: 24px;
  width: 100%;
}

.weather-ico,
.temperature-ico,
.wind-ico {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  width: 28%;
  height: 80%;
  background-color: #000;
  opacity: 0.3;
  transition: 0.7s;
}

.temperature-ico img {
  margin-right: 10px;
}

.weather-ico:hover,
.temperature-ico:hover,
.wind-ico:hover {
  opacity: 1;
}

.time {
  height: 20px;
}

footer {
  font-size: 14px;
  min-height: 100px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

/*------------------------------------ ANIMATIONS ------------------------------------*/
.animated {
  -webkit-animation-duration: 1.5s;
  animation-duration: 1.5s;
  -webkit-animation-fill-mode: both;
  animation-fill-mode: both;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }

  to {
    opacity: 1;
  }
}

.fadeIn {
  -webkit-animation-name: fadeIn;
  animation-name: fadeIn;
}

@-webkit-keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }

  25%,
  75% {
    opacity: 0;
  }
}

@keyframes flash {
  from,
  50%,
  to {
    opacity: 1;
  }

  25%,
  75% {
    opacity: 0;
  }
}

.flash {
  -webkit-animation-name: flash;
  animation-name: flash;
}

@-webkit-keyframes slideInUp {
  from {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

@keyframes slideInUp {
  from {
    -webkit-transform: translate3d(0, 100%, 0);
    transform: translate3d(0, 100%, 0);
    visibility: visible;
  }

  to {
    -webkit-transform: translate3d(0, 0, 0);
    transform: translate3d(0, 0, 0);
  }
}

.slideInUp {
  -webkit-animation-name: slideInUp;
  animation-name: slideInUp;
}
